<template>
  <div id='app'>
    <div class="loader" :class="{ 'is-paused fade': !this.loaded }"><div class="lds-ripple"><div></div><div></div></div></div>
    <div class="legend" :style="{ background: '#666 url(static/images/' + imageFilename + ') no-repeat 0px 50px' }">
      <dropdown-list :options="birdOptions" v-model="selectedBird"></dropdown-list>
      <h1 v-if="selectedCounty" :style="{ borderLeftColor: highlightedCounty.activeColor }">{{highlightedCounty.County}}</h1>
      <p v-if="selectedCounty"><span>{{highlightedCounty.percent_gardens_2018}}% of gardens</span></p>
    </div>
    <google-map name='bird' :counties="counties" :birdData="birdData" :selectedBird="selectedBird" @highlight="highlightCounty" @reset="resetCounty" @mapLoaded="mapIsLoaded" @mapLoading="mapIsLoading" />
  </div>
</template>
<script>

import GoogleMap from '@/components/GoogleMap'
import DropdownList from '@/components/DropdownList.vue'

export default {
  name: 'App',
  components: {
    GoogleMap,
    DropdownList
  },
  data () {
    return {
      loaded: false,
      selectedCounty: null,
      counties: [],
      birdData: [],
      birdOptions: [],
      selectedBird: 'Blackbird'
    }
  },
  methods: {
    highlightCounty: function (county) {
      this.selectedCounty = county
    },
    resetCounty: function () {
      this.selectedCounty = null
    },
    mapIsLoaded: function () {
      this.loaded = true
    },
    mapIsLoading: function () {
      this.loaded = false
    },
    setColor: function (percent) {
      if (percent > 95) {
        return '#990100'
      } else if (percent > 90) {
        return '#cc0001'
      } else if (percent > 85) {
        return '#fe0000'
      } else if (percent > 80) {
        return '#ff3300'
      } else if (percent > 75) {
        return '#ff6600'
      } else if (percent > 70) {
        return '#fe9900'
      } else if (percent > 65) {
        return '#ffcc00'
      } else if (percent > 60) {
        return '#fffc00'
      } else if (percent > 55) {
        return '#cbfe33'
      } else if (percent > 50) {
        return '#98ff66'
      } else if (percent > 45) {
        return '#67ff9a'
      } else if (percent > 40) {
        return '#33ffcc'
      } else if (percent > 35) {
        return '#00ffff'
      } else if (percent > 30) {
        return '#00ccff'
      } else if (percent > 25) {
        return '#0099ff'
      } else if (percent > 20) {
        return '#0166ff'
      } else if (percent > 15) {
        return '#0000fe'
      } else if (percent > 10) {
        return '#0033ff'
      } else if (percent > 5) {
        return '#0000cc'
      } else {
        return '#000098'
      }
    }
  },
  computed: {
    imageFilename: function () {
      var formattedString = this.selectedBird.replace(/\s+/g, '-').toLowerCase() + '.png'
      return formattedString
    },
    highlightedCounty: function () {
      var highlighted = this.birdData.find((item) => {
        return (
          (item.County.match(this.selectedCounty)) &&
          (item.Species.match(this.selectedBird))
        )
      })
      highlighted.activeColor = this.setColor(highlighted.percent_gardens_2018)
      return highlighted
    }
  },
  created () {
    fetch('static/birddata.json')
      .then(resp => resp.json())
      .then(data => {
        var uniqueBirds = []
        this.birdData = data
        data.filter(bird => {
          if (uniqueBirds.indexOf(bird.Species) === -1 && bird.percent_gardens_2018 > 40) {
            return uniqueBirds.push(bird.Species)
          }
        })
        this.birdOptions = uniqueBirds.sort()
      })

    fetch('static/counties.json')
      .then(resp => resp.json())
      .then(data => {
        this.counties = data
      })
  }
}
</script>

<style>
#app {
  font-family: 'Droid Serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

.legend {
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding-top: 250px;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 200px;
  z-index: 5;
}
.legend h1 {
  background-color: rgba(0,0,0,0.3);
  border-left-color:  rgba(0,0,0,0.3);
  border-left-style: solid;
  border-left-width: 10px;
  font-size: 16px;
  margin: 0;
  padding: 10px;
}
.legend span {
  color: #fff;
  font-size: 16px;
  font-weight: bold;
}
.legend p {
  background-color: rgba(0,0,0,0.8);
  color: #ccc;
  font-size: 14px;
  margin: 0;
  padding: 10px 0px 10px 20px;
}
.loader {
  -webkit-animation: fadeOut 3s ease-in 1 forwards;
  animation: fadeOut 3s ease-in 1 forwards;
  background-color: #18263b;
  clip: none;
  height: 100%;
  margin: 0;
  opacity: 1;
  position: relative;
  width: 100%;
  z-index: 5;
}
.lds-ripple {
  display: block;
  height: 64px;
  left: 50%;
  position: absolute;
  top: 50%;
  width: 64px;
  z-index: 5;
}
.lds-ripple div {
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  border-radius: 50%;
  border: 4px solid #ccc;
  opacity: 1;
  position: absolute;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  99% {
    opacity: 0;
  }
  100% {
    position: absolute;
    clip: rect(0px, 0px, 0px, 0px);
  }
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

.is-paused {
  animation-play-state: paused;
}
</style>



