<template>
  <div class='google-map' :id='mapName'></div>
</template>

<script>
export default {
  name: 'bird',
  props: ['name', 'counties', 'selectedBird', 'birdData'],
  watch: {
    selectedBird: function (newVal, oldVal) {
      this.setMapStyles()
    }
  },
  data: function () {
    return {
      vueGMap: null,
      mapName: this.name + '-map',
      API_KEY: 'AIzaSyABMn-MgNcLK-49_HUSgM1NaVE1DYopc9c'
    }
  },
  mounted: function () {
    this.createGoogleMaps()
      .then(this.initGoogleMaps, this.googleMapsFailedToLoad)
  },
  methods: {
    createGoogleMaps: function () {
      return new Promise((resolve, reject) => {
        let gmap = document.createElement('script')
        gmap.src = `https://maps.googleapis.com/maps/api/js?key=${this.API_KEY}`
        gmap.type = 'text/javascript'
        gmap.onload = resolve
        gmap.onerror = reject
        document.body.appendChild(gmap)
      })
    },
    initGoogleMaps: function () {
      this.requestLoading()
      const element = document.getElementById(this.mapName)
      const options = {
        zoom: 6,
        center: { lat: 56.0053834, lng: -2.5742888 },
        disableDefaultUI: true,
        styles: [
          {
            elementType: 'geometry',
            stylers: [
              { color: '#242f3e' }
            ]
          },
          {
            elementType: 'labels.text.stroke',
            stylers: [
              { color: '#242f3e' }
            ]
          },
          {
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#746855' }
            ]
          },
          {
            featureType: 'administrative.locality',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#d59563' }
            ]
          },
          {
            featureType: 'poi',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#d59563' }
            ]
          },
          {
            featureType: 'poi.park',
            elementType: 'geometry',
            stylers: [
              { color: '#263c3f' }
            ]
          },
          {
            featureType: 'poi.park',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#6b9a76' }
            ]
          },
          {
            featureType: 'road',
            elementType: 'geometry',
            stylers: [
              { color: '#38414e' }
            ]
          },
          {
            featureType: 'road',
            elementType: 'geometry.stroke',
            stylers: [
              { color: '#212a37' }
            ]
          },
          {
            featureType: 'road',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#9ca5b3' }
            ]
          },
          {
            featureType: 'road.highway',
            elementType: 'geometry',
            stylers: [
              { color: '#746855' }
            ]
          },
          {
            featureType: 'road.highway',
            elementType: 'geometry.stroke',
            stylers: [
              { color: '#1f2835' }
            ]
          },
          {
            featureType: 'road.highway',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#f3d19c' }
            ]
          },
          {
            featureType: 'transit',
            elementType: 'geometry',
            stylers: [
              { color: '#2f3948' }
            ]
          },
          {
            featureType: 'transit.station',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#d59563' }
            ]
          },
          {
            featureType: 'water',
            elementType: 'geometry',
            stylers: [
              { color: '#17263c' }
            ]
          },
          {
            featureType: 'water',
            elementType: 'labels.text.fill',
            stylers: [
              { color: '#515c6d' }
            ]
          },
          {
            featureType: 'water',
            elementType: 'labels.text.stroke',
            stylers: [
              { color: '#17263c' }]}
        ]
      }
      this.vueGMap = new google.maps.Map(element, options)
      this.vueGMap.data.loadGeoJson('../../static/counties.json')
      this.setMapStyles()
      this.requestLoaded()
    },
    googleMapsFailedToLoad: function () {
      this.vueGMap = 'Error occurred'
    },
    setMapStyles: function () {
      this.vueGMap.data.setStyle(feature => {
        var name = feature.getProperty('name')
        var color = '#000'
        this.birdData.map(bird => {
          if (bird.Species === this.selectedBird && bird.County === name) {
            feature.setProperty('bird', bird)
            color = this.setColor(bird.percent_gardens_2018)
          }
        })
        return {
          fillColor: color,
          fillOpacity: 0.8,
          strokeColor: '#000',
          strokeOpacity: 0.2,
          strokeWeight: 1
        }
      })
      this.vueGMap.data.addListener('mouseover', (event) => {
        var county = event.feature.getProperty('name')
        this.vueGMap.data.revertStyle()
        this.vueGMap.data.overrideStyle(event.feature, {fillOpacity: 1.0})
        this.requestHighlight(county)
      })
      this.vueGMap.data.addListener('mouseout', () => {
        this.vueGMap.data.revertStyle()
        this.requestReset()
      })
    },
    requestHighlight: function (county) {
      this.$emit('highlight', county)
    },
    requestReset: function () {
      this.$emit('reset')
    },
    setColor: function (percent) {
      if (percent > 95) {
        return '#990100'
      } else if (percent > 90) {
        return '#cc0001'
      } else if (percent > 85) {
        return '#fe0000'
      } else if (percent > 80) {
        return '#ff3300'
      } else if (percent > 75) {
        return '#ff6600'
      } else if (percent > 70) {
        return '#fe9900'
      } else if (percent > 65) {
        return '#ffcc00'
      } else if (percent > 60) {
        return '#fffc00'
      } else if (percent > 55) {
        return '#cbfe33'
      } else if (percent > 50) {
        return '#98ff66'
      } else if (percent > 45) {
        return '#67ff9a'
      } else if (percent > 40) {
        return '#33ffcc'
      } else if (percent > 35) {
        return '#00ffff'
      } else if (percent > 30) {
        return '#00ccff'
      } else if (percent > 25) {
        return '#0099ff'
      } else if (percent > 20) {
        return '#0166ff'
      } else if (percent > 15) {
        return '#0000fe'
      } else if (percent > 10) {
        return '#0033ff'
      } else if (percent > 5) {
        return '#0000cc'
      } else {
        return '#000098'
      }
    },
    requestLoaded: function () {
      this.$emit('mapLoaded')
    },
    requestLoading: function () {
      this.$emit('mapLoading')
    }
  }
}
</script>

<style scoped>
#bird-map {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  background: gray;
}
</style>



